.card {
    background-size: cover;
    background-position: center;
    background-clip: content-box;
    border: solid;
    min-height: 400px;
    width: 980px;
    /* box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); */
    position: relative;
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  
  .card .fa-spinner {
    font-size: 180px;
    position: absolute;
    left: 25%;
    top: 25%;
  }


